<template>
  <div class="info-recr">
    <div class="left">
      <div class="recr">
        <div class="name">{{recruiter.name}}</div>
        <div class="base">
          <p class="identity">{{recruiter.identity}}</p>
          <p><label for="">:</label>{{recruiter.address}}</p>
        </div>
        <div class="contact">
          <p><label for="">:</label>{{recruiter.email}}</p>
          <p><label for="">:</label>{{recruiter.phone}}</p>
        </div>
        <div class="fabu">
          <p><label for="">:</label>{{recruiter.schoolCnt}}</p>
          <p><label for="">:</label>{{recruiter.schoolCnt}}</p>
        </div>
      </div>
    </div>
    <div class="info">
      <!-- @tab-click="handleClick" -->
      <el-tabs v-model="activeName" >
        <el-tab-pane label="" name="first">
          <div class="main-interest">
            <teacher class="half" v-for="(teacher,index) in teachers" :key="index"
            :teacher="teacher"></teacher>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <div class="main-interest">
            <div class="school" @click="showSchool" v-for="idx in 5" :key="idx">
              <div class="school-name">{{school.name}}</div>
              <p><label for="">:</label>{{school.address}}</p>
              <p><label for="">:</label>{{school.jobCnt}}</p>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="third">
          <div class="main-interest">
            <essay class="half article" v-for="(index, o) in 9" :key="index"></essay>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="fourth">
          <div class="main-interest">
            <disguss class="half" v-for="(index,o) in 5" :key="index">
              <!-- <div slot="num"><div class="num"></div></div> -->
            </disguss>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

