<style lang="less" scoped>
.info-recr{
  width: 70%;
  margin: 20px auto;
  display: flex;
  .left{
    width: 28%;
    // background-color: #fff;
    margin-right: 20px;
    position: relative;
    // top: -50px;
    // width: 40%;
    // height: 900px;
    // background-color: yellow;
    display: flex;
    justify-content: center;
    .recr{
      border-top: #fba400 8px solid;
      box-shadow: 0 0 10px 0 #eee;
      margin-left: 10px;
      width: 100%;
      // height: 100%;
      background-color: #fff;
      padding: 5px 10px;
      .name{
        font-size: 22px;
        font-weight: 700;
        text-align: left;
        margin-top: 10px;
        margin-bottom: 8px;
      }
      p{
        text-align: left;
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        color: black;
        label{
          margin-right: 8px;
          font-weight: 700;
          color: #666;
        }
      }
      p.identity{
        font-size: 16px;
        font-weight: 400;
      }
      .fabu{
        border-top: 6px solid #f6f6f8;
        padding-top: 5px;
        // padding-left: 10px;
        p{
          cursor: pointer;
          text-decoration: underline;
          label{
            text-decoration-line: 0px;
          }
          &:hover{
            color: coral;
          }
        }
      }
    }
  }
  .info{
    width: 70%;
    padding: 20px;
    background-color: #fff;
  }
  
    .school{
      &:nth-of-type(1){
        border-top: 1px solid #eee;
      }
      cursor: pointer;
      border: 1px solid #eee;
      border-top: none;
      padding: 20px 0;
      // box-shadow: 0 0 10px 0 #eee;
      display: flex;

      .school-name{
        text-align: left;
        font-size: 20px;
        font-weight: 600;
        padding: 0px 50px;
      }
      p{
        margin-left: 100px;
      }
      &:hover{
        box-shadow: 0 0 10px 0 #eee;

      }
    }
}
</style>


